import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import axios from 'axios';
import './Login.css'
import { toast, ToastContainer } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

function Login(props) {
    let { setLogin } = props;
    let navigate = useNavigate();
    let [input, setInput] = useState({
        username: '',
        password: ''
    })
    let changeHandler = (e) => {
        setInput({
            ...input,
            [e.target.name]: e.target.value
        })
    }
    let submitHandler = async (e) => {
        e.preventDefault();
        axios.post("http://localhost:5600/login", input)
            .then((res) => {
                if (res.data) {
                    console.log(res.data , 'logged in after')
                    localStorage.setItem('jwt', res.data.jwt)
                    localStorage.setItem('payload', res.data.username)
                    toast.success(res.data.success)
                }
                setLogin(true)
                setTimeout(() => {
                    navigate('/mychat')
                }, 1000)

            }).catch((error) => {
                console.log(error)
                if (error.response.data.exists) {
                    toast.error(error.response.data.exists)
                }
                if (error.response.data.password) {
                    return toast.error(error.response.data.password)
                }
            })
    }
    return (
        <center className='register-section' style={{ 'margin': '5px auto', boxShadow: '4px 3px 5px black', padding: '20px', width: "50%" }}>
            <div className='icon-section-user' style={{ textAlign: 'center', color: 'tomato' }}>
                <svg xmlns="http://www.w3.org/2000/svg" width="76" height="79" fill="currentColor" className="bi bi-person" viewBox="0 0 16 16">
                    <path d="M8 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6m2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0m4 8c0 1-1 1-1 1H3s-1 0-1-1 1-4 6-4 6 3 6 4m-1-.004c-.001-.246-.154-.986-.832-1.664C11.516 10.68 10.289 10 8 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664z" />
                </svg>
            </div>
            <form onSubmit={submitHandler}>
                <div className="form-floating m-3" style={{ width: '80%' }}>
                    <input
                        type="text"
                        className="form-control"
                        id="floatingInput"
                        placeholder="name@example.com"
                        name='username'
                        onChange={changeHandler}
                    />
                    <label htmlFor="floatingInput">User name <span style={{ color: 'red' }}>*</span></label>
                </div>
                <div className="form-floating m-3" style={{ width: '80%' }}>
                    <input
                        type="password"
                        className="form-control"
                        id="floatingPassword"
                        placeholder="Password"
                        name='password'
                        onChange={changeHandler}
                    />
                    <label htmlFor="floatingPassword">Password <span style={{ color: 'red' }}>*</span></label>
                </div>
                <div className="col-auto register-btn">
                    <button type="submit" className="btn btn-primary m-3">Login</button>\
                    <center className='mt-1'>
                        <p style={{ color: '#fff', display: 'inline-block' }}>New user go for Register</p>
                        <button type="button" class="btn btn-outline-primary" style={{ display: 'inline-block', marginLeft: '10px' }} onClick={() => {
                            navigate('/register')
                        }}>Register</button>
                    </center>

                </div>
            </form>
            <ToastContainer />
        </center>
    )
}
export default Login